#App {
  width: 800px;
  height: 100%;
  display: flex;
  flex-direction: column;
}
#MainContainer{
  display: flex;
  align-items: center;
  justify-content: center;
}
#InnerContainer {
  display: flex;
  flex-direction: row;
}
.CapUp{text-transform: uppercase; }
.CapDown{text-transform: none;}

.HeaderM{margin: 0; padding: 0; text-transform: uppercase; color: #252525}
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

